/*
 *  Article tile
 *
 *  Version:
 *    1.0.0 - 2021/11/08
 */

.o-article-tile {
  display: block;
  margin: 0 auto 3.625rem;
  background-color: $s-color--background-subdued;

  // Image
  &__image {
    &-wrapper {
      margin-bottom: 2rem;
    }

    &-container {
      padding-bottom: 78.57%;
    }
  }

  // Super heading
  &__super-heading {
    margin-bottom: 0.5rem;
  }

  // Title
  &__title {
    margin-bottom: 0.875rem;
  }

  // Article info
  &__article-info {
    color: $s-color--font-focus;
    margin-bottom: 1rem;
  }

  // Description
  &__description {
    margin-bottom: 0;
  }

  /*** Media Queries ***/
  @include t-media-query($s-small-up) {
    max-width: 43.75rem;
  }

  @include t-media-query($s-medium-up) {
    width: auto;
    max-width: none;
    margin-bottom: 4.5rem;

    &__inner {
      display: grid;
      grid-template-columns: 48.86% 42.62%;
      gap: 8.52%;
      align-items: center;
    }

    &__image {
      &-wrapper {
        margin-bottom: 0;
      }

      &-container {
        padding-bottom: 65.12%;
      }
    }

    &__content {
      padding-right: 20%;
    }
  }

  @include t-media-query($s-large-up) {
    &__content {
      padding-right: 34.67%;
    }
  }

  @include t-media-query($s-xlarge-up) {
    margin-bottom: 7.5rem;

    &__title {
      margin-bottom: 1.0625rem;
    }
  }
}