/*
 *  Article next component
 *
 *  Version:
 *    1.0.0 - 2021/11/08
 */

.c-article-next {
  padding: 4.25rem $s-gutter--edge-mobile 11.375rem;
  background-color: $s-color--background-subdued;

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    &__btn-container {
      text-align: center;
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 5rem #{'min(3.6%, 7.5rem)'} 4rem;

    &__inner {
      max-width: 110rem;
      margin: 0 auto;
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 8.375rem #{'min(3.6%, 7.5rem)'} 15rem;
  }
}
